.App {
    color: var(--black);
    background: linear-gradient(
      106.37deg,
     
    );
    
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Inter', sans-serif;
  }
  .AppGlass {
    display: grid;
    height: 100%;
    width: 97%;
    background: var(--glass);
    border-radius: 2rem;
    gap: 16px;
    grid-template-columns: 11rem auto 20rem;
    overflow: hidden;
  }
  @media screen and (max-width: 1200px) {
    .AppGlass{
      grid-template-columns: 10% 50% auto;
      overflow-y: scroll;
    }
  }
  @media screen and (max-width: 768px) {
    .AppGlass{
      grid-template-columns: 1fr;
    }
  }
  /* sidebar */
  .sidebar {
    background-color:  rgba(217, 88, 88, 0.64);
  
    height: 180vh;
    margin-left: 0%;
    width: 150px;
    position: left;
    /* padding-top: 4rem; */
    /* transition: all 300ms ease; */
;
  }
  .donor{
    display: flex;
  }
  .bars{
    display: none;
  }
  .logo {
    display: flex;
    height: 5rem;
    font-weight: bold;
    font-size: 22px;
    gap: 1rem;
   
    align-items: center;
    justify-content: center;
    height: 4%;
    
  }
  
  .logo > span > span {
    color:#c23748;
  }
  .logo > img {
    width: 3rem;
    height: 3rem;
  }
  .menu {
    margin-top: 5rem;
    display: flex;
    
    
    flex-direction: column;
    gap: 4rem;
  }
  .menuItem {
    display: flex;
    align-items: center;
    gap: 1rem;
  
    font-weight: 500;
    height: 2.5rem;
    margin-left: 2rem;
    position: relative;
    transition: all 300ms ease;
    border-radius: 0.7rem;
    font-size: 18px;
    
  }
  .menuItem:hover {
    cursor: pointer;
  }
  .menu .menuItem:last-child {
    position: absolute;
    bottom: 2.3rem;
    width: 100%;
  }
  .active {
    background: var(--activeItem);
    margin-left: 10;
  }
  .active::before {
    content: "";
    width: 12px;
    height: 100%;
    background: var(--pink);
    margin-right: calc(1rem - 8px);
  }
  /* Tablets */
  @media screen and (max-width: 1200px) {
    .menuItem>span{
      display: none;
    }
    .logo{
      display: none;
    }
  }
  @media screen and (max-width: 768px) {
    .sidebar{
      position: fixed;
      /* background: #662525; */
      z-index: 9;
      background: #faf7f7;
      width: 55%;
      padding-right: 1rem;
      height: 100%;
    }
    .menuItem>span{
      
      display: block;
    }
    .logo{
      display: flex;
    }
    .menu .menuItem:last-child {
      position: relative;
      margin-top: 6rem;
    }
    .bars{
      display: flex;
      position: fixed;
      top: 2rem;
      left:60%;
      background:#ffe0e0;
      padding:10px;
      border-radius: 10px;
      z-index: 9;
    }
    .close{
      left: -60%;
    }
  }


/* Header */
.icons{
  font-size: 27px;
  cursor: pointer;
  color: rgb(34, 34, 34);
  gap: 2rem;
  display: flex;
  position: absolute;
  left: 80%;
  margin-top: 2%;
}


/* Footer */
.footer{
color: rgb(1, 0, 0);
position: absolute;
left: 45%;
margin-top: -40%;
}


/* card */
.CompactCard {
  display: flex;
  flex: 1;
  height: 7rem !important;
  border-radius: 0.7rem;
  color: white;
  padding: 1rem;
  position: relative;
  cursor: pointer;
  
}
.CompactCard:hover {
  box-shadow: none !important;
  
}

/* radial bar */
.radialBar {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 1rem;
}
.CircularProgressbar {
  width: 4rem !important;
  overflow: visible;
  
}
.CircularProgressbar-path {
  stroke: white !important;
  stroke-width: 12px !important;
  filter: drop-shadow(2px 4px 6px white);
}
.CircularProgressbar-trail {
  display: none;
}
.CircularProgressbar-text {
  fill: white !important;
}

.radialBar > span {
  font-size: 17px;
  
  font-weight: bold;
}

/* sideDetail*/
.detail {
  font-weight: 800;
  
  font-size: 30px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}

.detail > span:nth-child(2) {
  font-size: 22px;
  
  font-weight: bold;
}
.detail > span:nth-child(3) {
  font-size: 12px;
}
